.contact-container {
    width: 100vw;
    min-width: 100vw;
    max-width: 100vw;
    height: calc(100vh - var(--navbar-height));
}

.contact-wrapper {
    width: 1200px;
    max-width: 100vw;
    height: calc(100vh - var(--navbar-height));
    max-height: calc(100vh - var(--navbar-height));
    min-height: calc(100vh - var(--navbar-height));
    background-color: var(--wrapers-primary-bgc);
    margin: auto;
}